@charset "UTF-8";
/*style*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

@custom-media --mobileS (min-width: 320px);
@custom-media --mobileM (min-width: 375px);
@custom-media --mobileL (min-width: 465px);
@custom-media --tabletS (min-width: 600px);
@custom-media --tablet (min-width: 768px);
@custom-media --tabletL (min-width: 860px);
@custom-media --laptop (min-width: 1200px);
@custom-media --laptopL (min-width: 1560px);
@custom-media --fourK (min-width: 2560px);
@custom-media --maxMobile (max-width: 767px);
@lost gutter 12px;
@font-face {
	font-family: 'Noto Sans';
	src: url("assets/fonts/noto-sans/NotoSans-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'Noto Sans';
	src: url("assets/fonts/noto-sans/NotoSans-Italic.woff") format("woff");
	font-weight: 400;
	font-style: italic;
}

@font-face {
	font-family: 'Noto Sans';
	src: url("assets/fonts/noto-sans/NotoSans-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

@font-face {
	font-family: 'Noto Sans';
	src: url("assets/fonts/noto-sans/NotoSans-BoldItalic.woff") format("woff");
	font-weight: 700;
	font-style: italic;
}

@font-face {
	font-family: 'Source Sans Pro';
	src: url("assets/fonts/source-code-pro/SourceCodePro-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

body {
	background-color: #fafafc;
	color: #1d1d1d;
	font-family: "Noto Sans", sans-serif;
	font-size: 16px;
	line-height: 1.5;
}

h1, h2 {
	margin: 60px 0 20px;
}

h1, h2, h3, h4 {
	color: #005a6a;
}

h1 {
	font-size: 2.375rem;
	font-weight: 400;
	line-height: 1.2;
}

h2 {
	font-size: 22px;
	line-height: 1.2;
	font-weight: 700;
	border-bottom: 1px solid #dddddd;
}

h3,
summary {
	margin: 40px 0 20px;
	font-size: 18px;
	font-weight: 700;
}

img {
	max-width: 100%;
}

p {
	margin: 16px 0;
	font-size: 16px;
	line-height: 24px;
}

p + ol,
p + ul {
	margin-top: -8px;
}

strong {
	font-weight: 700;
}

em {
	font-style: italic;
}

a {
	color: #003366;
}

a:hover, a:focus {
	color: #005a6a;
}

[tabindex]:not([tabindex="-1"]),
a,
button,
input,
select,
textarea {
	outline: 2px solid transparent;
	outline-color: transparent;
	outline-offset: 5px;
	transition: outline-offset 0.3s linear;
}

[tabindex]:not([tabindex="-1"]):focus,
a:focus,
button:focus,
input:focus,
select:focus,
textarea:focus {
	outline-color: #005a6a;
	outline-offset: 2px;
}

ul {
	list-style-type: disc;
}

ul ul {
	list-style-type: circle;
}

ul ul ul {
	list-style-type: square;
}

ol,
ul {
	padding-left: 32px;
}

ol li,
ul li {
	margin-bottom: 8px;
}

ol li p,
ul li p {
	margin: 0;
}

ol ol,
ol ul,
ul ol,
ul ul {
	margin-top: 8px;
}

details {
	padding-left: 32px;
}

details > summary:first-of-type {
	list-style-type: none;
}

details[open] > summary:before {
	content: "â€“";
}

summary {
	color: #005a6a;
	cursor: pointer;
}

summary:before {
	content: "+";
	margin-right: 8px;
	display: inline-block;
	border-radius: 5px;
	width: 20px;
	height: 20px;
	line-height: .9;
	background: #ffffff;
	color: #005a6a;
	border: 2px solid #005a6a;
	text-align: center;
	font-weight: 700;
	transition: all 0.3s linear;
}

summary:focus:before, summary:hover:before {
	border-color: #003366;
	background: #003366;
	color: #ffffff;
}

figure {
	margin: 0 0 16px;
	padding: 10px;
	width: min-content;
	max-width: 100%;
	border: 1px solid #dddddd;
	border-radius: 3px;
	background-color: #ffffff;
}

figure p {
	margin: 0;
}

figure img {
	max-width: none;
	border: 1px solid #dddddd;
}

figcaption {
	margin: 0;
	font-weight: 400;
}

code {
	font-family: "Source Sans Pro", sans-serif;
}

/* global classes */
.content {
	position: relative;
}

.wrapper {
	width: 90%;
	margin: 0 auto;
	transition: width .2s ease-in-out;
}

.wrapper::after {
	clear: both;
	content: "";
	display: table;
}

@media (--laptop) {
	.wrapper {
		width: 1140px;
		margin: 0 auto;
	}
	.wrapper::after {
		clear: both;
		content: "";
		display: table;
	}
}

.box {
	margin-top: 16px;
	border: 1px solid #dddddd;
	background-color: #ffffff;
}

.box + .box {
	margin-top: 0;
	border-top: 0;
}

.button {
	padding: 8px 12px;
	border: 2px solid #005a6a;
	border-radius: 5px;
	display: inline-block;
	background-color: #005a6a;
	color: #ffffff;
	cursor: pointer;
	font-size: 13px;
	font-weight: 700;
	line-height: 1.4;
	text-decoration: none;
}

.button.is-visible {
	opacity: 1;
}

.button--backtotop {
	position: fixed;
	right: 16px;
	bottom: 16px;
	border-radius: 100px;
	border-color: #003366;
	background-color: #003366;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
	opacity: 0;
	transition: opacity .5s linear;
}

.button.button--secondary {
	background-color: #ffffff;
	color: #005a6a;
}

.button.button--secondary:hover {
	color: #ffffff;
	border-color: #003366;
	background-color: #003366;
}

.button.button--secondary:focus {
	color: #005a6a;
}

.button--small {
	padding: 2px 4px;
}

.button:hover, .button:focus {
	color: #ffffff;
}

.button:focus {
	outline-color: #003366;
}

.button svg {
	width: 16px;
	height: 16px;
	display: inline-block;
	fill: #ffffff;
}

.button:disabled {
	opacity: .75;
	pointer-events: none;
	text-decoration: line-through;
}

.button:disabled:focus {
	background-color: #003366;
	color: #ffffff;
}

.intro__buttons {
	margin: 16px 0;
}

.intro__head {
	background-color: #fafafc;
}

.intro header {
	padding: 8px 16px 2px;
	color: #005a6a;
	font-size: 14px;
	font-weight: 700;
	line-height: 1.6;
}

.intro header.intro__head {
	padding: 8px 16px;
}

.intro header h3 {
	margin: 0;
	font-size: 16px;
}

.intro p {
	padding-right: 16px;
	padding-left: 16px;
}

.intro ol, .intro ul {
	padding-left: 48px;
}

.intro ol ol, .intro ol ul, .intro ul ol, .intro ul ul {
	padding-left: 32px;
}

.section {
	overflow: hidden;
}
