$font-primary: 'Noto Sans', sans-serif;
$font-code: 'Source Sans Pro', sans-serif;

@font-face {
  font-family: 'Noto Sans';
  src: url('assets/fonts/noto-sans/NotoSans-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Noto Sans';
  src: url('assets/fonts/noto-sans/NotoSans-Italic.woff') format('woff');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Noto Sans';
  src: url('assets/fonts/noto-sans/NotoSans-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Noto Sans';
  src: url('assets/fonts/noto-sans/NotoSans-BoldItalic.woff') format('woff');
  font-weight: 700;
  font-style: italic;
}

@font-face {
  font-family: 'Source Sans Pro';
  src: url('assets/fonts/source-code-pro/SourceCodePro-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
