.intro {
  &__buttons {
    margin: 16px 0;
  }

  &__head {
    background-color: $color-snow;
  }

  header {
    padding: 8px 16px 2px;
    color: $color-green;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.6;

    &.intro__head {
      padding: 8px 16px;
    }

    h3 {
      margin: 0;
      font-size: 16px;
    }
  }

   p {
     padding-right: 16px;
     padding-left: 16px;
   }

  ol, ul {
    padding-left: 48px;

    ol, ul {
      padding-left: 32px;
    }
  }


}
