
.button {
  padding: 8px 12px;
  border: 2px solid $color-green;
  border-radius: 5px;
  display: inline-block;
  background-color: $color-green;
  color: $color-white;
  cursor: pointer;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.4;
  text-decoration: none;

  &.is-visible {
    opacity: 1;
  }

  &--backtotop {
    position: fixed;
    right: 16px;
    bottom: 16px;
    border-radius: 100px;
    border-color: $color-ocean;
    background-color: $color-ocean;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.5);
    opacity: 0;
    transition: opacity .5s linear;
  }

  &.button--secondary {
    background-color: $color-white;
    color: $color-green;

    &:hover {
      color: $color-white;
      border-color: $color-ocean;
      background-color: $color-ocean;
    }

    &:focus {
      color: $color-green;
    }
  }

  &--small{
    padding: 2px 4px;
  }

  &:hover,
  &:focus {
    color: $color-white;
  }

  &:focus {
    outline-color: $color-ocean;
  }

  svg {
    width: 16px;
    height: 16px;
    display: inline-block;
    fill: $color-white;
  }

  &:disabled {
    opacity: .75;
    pointer-events: none;
    text-decoration: line-through;

    &:focus {
      background-color: $color-ocean;
      color: $color-white;
    }
  }
}
