body {
  background-color: $color-snow;
  color: $color-dark;
  font-family: $font-primary;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2 {
  margin: 60px 0 20px;
}

h1, h2, h3, h4 {
  color: $color-green;
}

h1 {
  font-size: 2.375rem;
  font-weight: 400;
  line-height: 1.2;
}

h2 {
  font-size: 22px;
  line-height: 1.2;
  font-weight: 700;
  border-bottom: 1px solid $color-silver;
}

h3,
summary {
  margin: 40px 0 20px;
  font-size: 18px;
  font-weight: 700;
}

img {
  max-width: 100%;
}

p {
  margin: 16px 0;
  font-size: 16px;
  line-height: 24px;

  & + ol,
  & + ul {
    margin-top: -8px;
  }
}

strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

a {
  color: $color-ocean;

  &:hover,
  &:focus {
    color: $color-green;
  }
}

[tabindex]:not([tabindex="-1"]),
a,
button,
input,
select,
textarea {
  outline: 2px solid transparent;
  outline-color: transparent;
  outline-offset: 5px;
  transition: outline-offset $transition-duration $transition-effect;

  &:focus {
    outline-color: $color-green;
    outline-offset: 2px;
  }
}

ul {
  list-style-type: disc;

  ul {
    list-style-type: circle;

    ul {
      list-style-type: square;
    }
  }
}

ol,
ul {
  padding-left: 32px;

  li {
    margin-bottom: 8px;

    p {
      margin: 0;
    }
  }

  & ol,
  & ul {
    margin-top: 8px;
  }
}

details {
  padding-left: 32px;

  & > summary {
    &:first-of-type {
      list-style-type: none;
    }
  }

  &[open] {
    & > summary {
      &:before {
        content: "â€“";
      }
    }
  }
}

summary {
  color: $color-green;
  cursor: pointer;

  &:before {
    content: "+";
    margin-right: 8px;
    display: inline-block;
    border-radius: 5px;
    width: 20px;
    height: 20px;
    line-height: .9;
    background: $color-white;
    color: $color-green;
    border: 2px solid $color-green;
    text-align: center;
    font-weight: 700;
    transition: all $transition-duration $transition-effect;
  }

  &:focus,
  &:hover {
    &:before {
      border-color: $color-ocean;
      background: $color-ocean;
      color: $color-white;
    }
  }
}

figure {
  margin: 0 0 16px;
  padding: 10px;
  width: min-content;
  max-width: 100%;
  border: 1px solid $color-silver;
  border-radius: 3px;
  background-color: $color-white;

  p {
    margin: 0;
  }

  img {
    max-width: none;
    border: 1px solid $color-silver;
  }
}

figcaption {
  margin: 0;
  font-weight: 400;
}

code {
  font-family: $font-code;
}

/* global classes */

.content {
  position: relative;
}

.wrapper {
  @include container;
}

.box {
  margin-top: 16px;
  border: 1px solid $color-silver;
  background-color: $color-white;

  & + .box {
    margin-top: 0;
    border-top: 0;
  }
}
